<template>
  <div
    v-loading="loading"
    class="data-source"
  >
    <gs-form
      ref="form"
      :model="form"
      :rules="rules"
      label-position="top"
    >
      <gs-form-item
        label="数据库"
        prop="dbGuid"
      >
        <gs-select
          :disabled="columnLoading || tableLoading"
          v-model="form.dbGuid"
          searchable
          @change="dbSelectChange"
        >
          <gs-option
            v-for="item in databaseOptions"
            :key="item.databaseId"
            :label="item.databaseName"
            :value="item.databaseId"
          />
        </gs-select>
      </gs-form-item>
      <gs-form-item
        label="数据表"
        prop="tableGuid"
      >
        <gs-select
          :disabled="columnLoading"
          v-model="form.tableGuid"
          searchable
          @visible-change="tableSelectVisibleChange"
        >
          <gs-option
            v-for="item in tableOptions"
            :key="item.tableGuid"
            :label="item.tableName"
            :value="item.tableGuid"
          />
        </gs-select>
      </gs-form-item>
      <gs-form-item
        label="最大结果行数"
        prop="maxRowNumber"
      >
        <gs-input-number
          v-model="form.maxRowNumber"
          :min="1"
          :max="2000"
        />
      </gs-form-item>
      <gs-form-item
        label="字段"
        prop="column"
      >
        <div class="column">
          <gs-modal-select
            v-model="form.column"
            :loading="columnLoading"
            :right-selected-render="rightSelectedRender"
            flatten
            @change="handleCheckedColumnChange"
          >
            <gs-option
              v-for="item in columnOptions"
              :key="item.columnId"
              :label="item.columnName"
              :value="item.columnName"
              :desc="item.columnId"
            />
          </gs-modal-select>
          <div class="line" />
          <div class="desc-label">描述</div>
        </div>
      </gs-form-item>
    </gs-form>
    <div
      v-show="columnOptions && columnOptions.length"
      class="choose-all-fileds"
    >
      <gs-checkbox
        :indeterminate="isIndeterminate"
        v-model="checkAll"
        @change="handleCheckAllChange"
      >全选</gs-checkbox>
    </div>
    <div class="operation">
      <gs-button
        type="primary"
        @click="handleNext"
      >下一步</gs-button>
    </div>
  </div>
</template>
<script>
import mixins from '../mixin.js';
import * as service from 'src/service/data-share/index';
export default {
  mixins: [mixins],
  props: {
    data: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    const numberValidator = (rule, v, cb) => {
      if (v === '') {
        cb(new Error('请填写最大结果行数'));
      }
      if (v && /\d/.test(v)) {
        cb();
      } else {
        cb(new Error('最大结果行数必须为数字且不能为0'));
      }
    };
    return {
      form: {
        dbGuid: '',
        tableGuid: '',
        column: [],
        maxRowNumber: 10
      },
      loading: false,
      tableOptions: [],
      databaseOptions: [],
      columnOptions: [],
      columnLoading: false,
      tableLoading: false,
      rules: {
        dbGuid: [{required: true, trigger: 'change', message: '请选择数据库'}],
        tableGuid: [{required: true, trigger: 'change', message: '请选择数据表'}],
        column: [{required: true, trigger: 'change', message: '请选择字段', type: 'array'}],
        maxRowNumber: [
          {required: true, trigger: 'blur', validator: numberValidator}
        ]
      },
      isIndeterminate: false,
      checkAll: false
    };
  },
  watch: {
    'form.tableGuid': {
      handler(val) {
        if (val) {
          this.form.column = [];
          this.$nextTick(_ => {
            this.$refs.form.clearValidate('column');
          });
          this.fetchColumn();
        }
      }
    }
  },
  created() {
    this.fetchDatabase().then(_ => {
      if (this.data && Object.keys(this.data).length) {
        this.setData();
      }
    });
  },
  methods: {
    setData() {
      const {dbGuid, tableGuid, maxRowNumber, fields} = this.data;
      const column = fields.map(item => item.columnName);
      this.form = {
        dbGuid,
        tableGuid,
        maxRowNumber,
        column: []
      };
      this.$nextTick(_ => {
        this.$refs.form.clearValidate('column');
      });
      const hasDBAuth = this.databaseOptions.find(i => i.databaseId === this.form.dbGuid);
      if (!hasDBAuth) {
        this.form.dbGuid = '';
        this.form.tableGuid = '';
        this.$Message.warning('无该库权限');
      }
      this.tableSelectVisibleChange(true);
      if (hasDBAuth) {
        this.fetchColumn().then(_ => {
          this.$nextTick(_ => {
            this.form.column = column;
          });
        });
      }
    },
    fetchDatabase() {
      this.loading = true;
      return service.getDataBase({
        offset: 0,
        limit: -1,
        query: ''
      }).then(data => {
        this.databaseOptions = data.lists;
        this.loading = false;
      });
    },
    fetchTable() {
      this.tableLoading = true;
      return service.getTable({
        dbGuid: this.form.dbGuid,
        offset: 0,
        limit: -1,
        query: ''
      }).then(data => {
        this.tableOptions = data.lists;
        this.tableLoading = false;
        const hasTableAuth = this.tableOptions.find(i => i.tableGuid === this.form.tableGuid);
        if (!hasTableAuth) {
          this.form.tableGuid = '';
        }
      });
    },
    fetchColumn() {
      this.columnLoading = true;
      return service.getColumn(this.form.tableGuid).then(data => {
        this.columnOptions = data;
        this.columnLoading = false;
        return data;
      });
    },
    tableSelectVisibleChange(val) {
      if (val) {
        if (this.form.dbGuid) {
          this.fetchTable();
          this.handleCheckedColumnChange([]);
        } else {
          this.$refs.form.validateField('dbGuid');
        }
      }
    },
    rightSelectedRender(h, option) {
      const optionItem = this.columnOptions.find(i => option.value === i.columnName);
      return (
        <div>
          <span
            class="label"
            title={optionItem.columnName}
          >{optionItem.columnName}</span>
          <span
            class="desc"
            title={optionItem.description}
          >{optionItem.description || '-'}</span>
          <i class="gs-icon-close" on-click={this.unselectOption.bind(this, option)} />
        </div>
      );
    },
    unselectOption(option) {
      const index = this.form.column.findIndex(i => i === option.value);
      this.form.column.splice(index, 1);
    },
    handleNext() {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.saveApiMsg({
            ...this.form,
            column: this.columnOptions.filter(i => this.form.column.includes(i.columnName))
          });
          this.$emit('next');
        }
      });
    },
    dbSelectChange() {
      this.form.tableGuid = '';
      this.form.column = [];
      this.columnOptions = [];
    },
    handleCheckAllChange(event) {
      this.form.column = event.target.checked ? this.columnOptions.map(item => item.columnName) : [];
      this.isIndeterminate = false;
    },
    handleCheckedColumnChange(value) {
      const checkedCount = value.length;
      this.checkAll = checkedCount > 0;
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.columnOptions.length;
    }
  }
};
</script>



