
import * as service from 'src/service/data-share/index';
import axios from 'axios';
import {NUMBER_TYPE} from 'src/utils';
let randomName = Math.random().toString(36).slice(-8);
export default {
  data() {
    return {
      testData: [],
      limit: 10,
      offset: 0,
      btnLoading: false,
      activeName: 'json',
      testRes: [],
      numberType: NUMBER_TYPE
    };
  },
  computed: {
    tableHeader() {
      const {testRes} = this;
      if (testRes && testRes[0]) {
        return Object.keys(testRes[0]);
      } else {
        return [];
      }
    },
    testResStringify() {
      return JSON.stringify(this.testRes, null, 4);
    }
  },
  methods: {
    checkTestInput() {
      if (this.offset === '' || this.limit === '') {
        this.$Message.error(`请填入limit和offset参数。`);
        return false;
      }
      const fill = this.testData.filter(item => item.fill && !item.check);
      if (fill && fill.length) {
        const str = fill.map(i => i.columnName).join(',');
        this.$Message.error(`${str}字段为必填字段，请选中并输入数据`);
        return false;
      }
      const checkStr = this.testData.filter(item => item.check)
        .filter(item => {
          if (
            (this.numberType.includes(item.type) && item.value === '') ||
          (item.type === 'timestamp' && item.value === '') ||
          (item.type === 'boolean' && item.value === '')
          ) {
            return true;
          } else {
            return false;
          }
        })
        .map(i => i.columnName).join(',');
      if (checkStr) {
        this.$Message.error(`${checkStr}字段缺少过滤值内容，请输入`);
        return false;
      }
      return true;
    },
    stopTest() {
      this.source.cancel();
      service.stopTestApi(randomName).then(data => {
        this.$Message.success(`停止成功`);
      });
    },
    startTest() {
      if (this.checkTestInput()) {
        const {
          tableGuid,
          dbGuid,
          maxRowNumber
        } = this.detailData;
        const parameter = this.testData.filter(item => item.check)
          .map(item => {
            if (item.type === 'timestamp' && item.value) {
              return {columnName: item.columnName, value: [item.value.unix()]};
            }
            return {columnName: item.columnName, value: [item.value]};
          });
        const queryFields = this.detailData.fields.map(item => item.columnName);
        const params = {
          tableGuid,
          dbGuid,
          maxRowNumber,
          parameter,
          queryFields,
          limit: this.limit,
          offset: this.offset
        };
        randomName = Math.random().toString(36).slice(-8);
        this.btnLoading = true;
        const { CancelToken } = axios;
        this.source = CancelToken.source();
        service.testApi({params, randomName}, this.source.token).then(data => {
          this.testRes = data;
          this.btnLoading = false;
        }).catch(e => {
          this.btnLoading = false;
          console.log('Request canceled', e.message);
        });
      }
    }
  }
}
;



