<template>
  <div class="create-api">
    <gs-button
      type="primary"
      icon="revoke"
      class="revoke"
      @click="returnList"
    />
    <gs-steps
      :active="activeStep"
      align-center
      space="160px"
      size="small"
    >
      <template slot="header">
        <gs-step
          v-for="(item, index) in stepsTitle"
          :key="index"
          :title="item"
        />
      </template>
      <template slot="content">
        <div
          v-loading="loading"
          class="steps-content"
        >
          <transition
            name="fade-transform-right"
            mode="out-in"
          >
            <keep-alive>
              <component
                v-if="hasData"
                :is="component"
                :is-edit="isEdit"
                :data="apiData"
                :active-step="activeStep"
                @next="handleNext"
                @back="handlePrev"
              />
            </keep-alive>
          </transition>
        </div>
      </template>
    </gs-steps>
  </div>
</template>
<script>
import './style.scss';
import dataSource from './step1';
import baseInfo from './step2';
import apiConfig from './step3';
import apiTest from './step4';
import * as service from 'src/service/data-share/index';
import mixin from './mixin.js';
const STEPS_TITLE = ['选择数据源', '基本信息', 'API配置信息', '测试'];
export default {
  components: {
    dataSource,
    baseInfo,
    apiConfig,
    apiTest
  },
  mixins: [mixin],
  data() {
    return {
      activeStep: 0,
      stepsTitle: STEPS_TITLE,
      components: ['dataSource', 'baseInfo', 'apiConfig', 'apiTest'],
      isEdit: false,
      apiData: null,
      allDone: false,
      loading: false
    };
  },
  computed: {
    component() {
      return this.components[this.activeStep];
    },
    hasData() {
      if (this.$route.query.apiId) {
        return this.apiData !== null;
      } else {
        return true;
      }
    }
  },
  // beforeRouteLeave(to, from, next) {
  //   if (this.allDone) {
  //     next();
  //   } else {
  //     this.$Modal.confirm({
  //       title: '提示',
  //       content: '离开当前页面数据会丢失，是否确认离开？',
  //       onOk: () => {
  //         next();
  //       },
  //       onCancel: () => {
  //         next(false);
  //       }
  //     });
  //   }
  // },
  created() {
    this.fetchApiDetail();
  },
  methods: {
    handleNext() {
      if (this.activeStep < 3) {
        this.activeStep = this.activeStep + 1;
      } else {
        const params = JSON.parse(JSON.stringify(this.storeApiMsg));
        delete params.column;
        const {isEdit, apiId} = this.$route.query;
        this.loading = true;
        if (isEdit && apiId) {
          service.editApi({params, apiId}).then(data => {
            this.$Message.success(`修改成功`);
            this.allDone = true;
            this.loading = false;
            this.$router.push({
              name: '/main/data-share',
              query: {
                groupId: this.$route.query.groupId
              }
            });
          }).catch(_ => {
            this.loading = false;
          });
        } else {
          service.createApi(params).then(data => {
            this.allDone = true;
            this.loading = false;
            this.$Message.success(`创建成功`);
            this.$router.push({
              name: '/main/data-share',
              query: {
                groupId: this.$route.query.groupId
              }
            });
          }).catch(_ => {
            this.loading = false;
          });
        }
      }
    },
    handlePrev() {
      this.activeStep = this.activeStep - 1;
    },
    fetchApiDetail() {
      if (this.$route.query.apiId) {
        service.getApiDetail(this.$route.query.apiId)
          .then(data => {
            this.apiData = data;
          });
      }
    },
    returnList() {
      this.$router.push({
        path: '/main/data-share/list',
        query: {
          groupId: this.$route.query.groupId
        }
      });
    }
  }
};
</script>



