<template>
  <gs-modal
    v-model="modalVisible"
    title="过滤条件字段"
    @confirm="confirmEditField"
    @cancel="cancel"
  >
    <gs-form
      :model="form"
      label-width="120px"
    >
      <gs-form-item
        label="字段名称"
        prop="columnName"
      >
        <span>{{ form.columnName }}</span>
      </gs-form-item>
      <gs-form-item
        label="字段类型"
        prop="type"
      >
        <span>{{ form.type }}</span>
      </gs-form-item>
      <gs-form-item
        label="必传"
        prop="fill"
      >
        <gs-radio-group v-model="form.fill">
          <gs-radio :label="true">是</gs-radio>
          <gs-radio :label="false">否</gs-radio>
        </gs-radio-group>
      </gs-form-item>
      <gs-form-item
        v-if="!form.fill"
        label="是否有缺省默认值"
        prop="useDefaultValue"
      >
        <gs-radio-group
          v-model="form.useDefaultValue"
          @change="useDefaultValueChange"
        >
          <gs-radio :label="true">是</gs-radio>
          <gs-radio :label="false">否</gs-radio>
        </gs-radio-group>
      </gs-form-item>
      <gs-form-item
        v-if="!form.fill"
        label="默认值"
        prop="defaultValue"
      >
        <fields-input
          v-model="form.defaultValue"
          :type="form.type"
          :disabled="!form.useDefaultValue"
        />
      </gs-form-item>
    </gs-form>
  </gs-modal>
</template>
<script>
import fieldsInput from 'src/components/fields-input';
export default {
  components: {
    fieldsInput
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    data: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      modalVisible: false,
      form: {}
    };
  },
  watch: {
    data(val) {
      this.form = val;
    },
    show(val) {
      this.modalVisible = val;
    }
  },
  methods: {
    confirmEditField() {
      this.$emit('confirm', this.form);
    },
    cancel() {
      this.$emit('update:show', false);
    },
    useDefaultValueChange(val) {
      if (!val) {
        this.form.defaultValue = '';
      }
    }
  }
};
</script>




