<template>
  <gs-date-picker
    v-if="type === 'timestamp'"
    :value="valueCache"
    :disabled="disabled"
    type="date-time"
    @change="change"
  />
  <gs-radio-group
    v-else-if="type === 'boolean'"
    :value="valueCache"
    :disabled="disabled"
    @change="change"
  >
    <gs-radio :label="true" />
    <gs-radio :label="false" />
  </gs-radio-group>
  <!-- <gs-input
    v-else-if="isNumber"
    :value="valueCache"
    :max="9007199254740991"
    :disabled="disabled"
    class="fileds-input-number"
    @change="change"
  /> -->
  <gs-input
    v-else
    :placeholder="isNumber ? '请输入数字': ''"
    :value="valueCache"
    :disabled="disabled"
    class="fileds-input"
    @change="change"
  />
</template>
<script>
import moment from 'moment';
import {NUMBER_TYPE} from 'src/utils';
export default {
  name: 'FieldsInput',
  props: {
    value: {
      type: [String, Number, Object, Array, Boolean],
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      numberType: NUMBER_TYPE
    };
  },
  computed: {
    isNumber() {
      return this.numberType.some(i => this.type.startsWith(i));
    },
    valueCache() {
      if (this.type === 'timestamp' && this.value !== '') {
        return moment(this.value);
      } else {
        return this.value;
      }
    }
  },
  methods: {
    change(val) {
      let result = '';
      if (this.type === 'timestamp') {
        result = val.valueOf();
      } else if (this.isNumber) {
        result = Number.isSafeInteger(parseFloat(val)) ? parseFloat(val) : val;
      } else {
        result = val;
      }
      this.$emit('input', result);
    }
  }
};
</script>
<style lang="scss">
.fileds-input ,
.fileds-input-number{
  width: 300px;
}
</style>




