<template>
  <div class="api-config">
    <div class="content">
      <div class="all-fileds">
        <div class="all-fileds-header">
          <h4 class="title">
            已选字段
          </h4>
          <div
            v-show="storeApiMsg.column && storeApiMsg.column.length"
            class="choose-all-fileds"
          >
            <gs-checkbox
              :indeterminate="isIndeterminate"
              v-model="checkAll"
              @change="handleCheckAllChange"
            >全选</gs-checkbox>
          </div>
        </div>
        <ul class="container">
          <li
            v-for="(item, index) in storeApiMsg.column || []"
            :key="index"
            :class="fieldsName.includes(item.columnName) ? 'active' : ''"
            @click="chooseFields(item)"
          >
            <i
              v-if="fieldsName.includes(item.columnName)"
              class="gs-icon-check"
            />
            <span>{{ item.columnName }}</span>
            <i
              class="gs-icon-close"
              @click.stop="deleteField(item.columnName, index)"
            />
          </li>
        </ul>
      </div>
      <div class="filter-fileds">
        <h4 class="title">
          过滤条件字段
        </h4>
        <div class="container">
          <el-table
            :data="fields"
            style="width: 100%"
          >
            <el-table-column
              prop="columnName"
              label="名称"
              show-overflow-tooltip
            />
            <el-table-column
              prop="fill"
              label="必传"
              show-overflow-tooltip
            >
              <template slot-scope="scope">
                <span>{{ scope.row.fill ? '是' : '否' }}</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="type"
              label="类型"
              show-overflow-tooltip
            />
            <el-table-column
              prop="defaultValue"
              label="默认值"
              show-overflow-tooltip
            >
              <template slot-scope="scope">
                <span>{{ scope.row.defaultValue || '无' }}</span>
              </template>
            </el-table-column>
            <el-table-column
              label="操作"
            >
              <template slot-scope="scope">
                <div>
                  <gs-button
                    type="text-primary"
                    @click="editFilterField(scope)"
                  >编辑</gs-button>
                  <gs-button
                    type="text-primary"
                    @click="deleteField(scope.row.columnName)"
                  >删除</gs-button>
                </div>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
    <div class="operation">
      <gs-button
        @click="handleBack"
      >上一步</gs-button>
      <gs-button
        type="primary"
        @click="handleNext"
      >下一步</gs-button>
    </div>
    <add-default-value
      :show.sync="modalVisible"
      :data="form"
      @confirm="confirmEditField"
    />
  </div>
</template>
<script>
import mixins from '../mixin.js';
import addDefaultValue from './add-default-value-modal.vue';
export default {
  components: {
    addDefaultValue
  },
  mixins: [mixins],
  props: {
    data: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      fields: [],
      form: {},
      modalVisible: false,
      editIndex: -1,
      isIndeterminate: false,
      checkAll: false
    };
  },
  computed: {
    fieldsName() {
      return this.fields.map(i => i.columnName);
    }
  },
  watch: {
    'storeApiMsg.column': {
      handler(val, oldval) {
        const equal = JSON.stringify(val) === JSON.stringify(oldval);
        if (!equal) {
          this.fields = [];
          this.changeCheckAllStatus();
        }
      },
      deep: true
    }
  },
  created() {
    if (this.data && Object.keys(this.data).length) {
      this.setData();
    }
  },
  methods: {
    setData() {
      const columnNames = this.storeApiMsg.column.map(i => i.columnName);
      this.fields = (this.data.fields || [])
        .filter(item => item.filter && columnNames.includes(item.columnName));
      this.changeCheckAllStatus();
    },
    handleNext() {
      let arr = [];
      this.storeApiMsg.column.map(item => {
        if (!this.fieldsName.includes(item.columnName)) {
          arr.push({
            columnName: item.columnName,
            filter: false,
            type: item.type
          });
        }
      });
      this.saveApiMsg({
        fields: [...arr, ...this.fields]
      });
      this.$emit('next');
    },
    handleBack() {
      this.$emit('back');
    },
    chooseFields(item) {
      if (!this.fieldsName.includes(item.columnName)) {
        this.fields.push({
          columnName: item.columnName,
          filter: true,
          fill: false,
          defaultValue: '',
          type: item.type,
          useDefaultValue: false
        });
        this.changeCheckAllStatus();
      }
    },
    deleteField(name, i) {
      const index = this.fields.findIndex(i => name === i.columnName);
      if (index !== -1) {
        this.fields.splice(index, 1);
        this.changeCheckAllStatus();
      }
    },
    editFilterField(scope) {
      this.modalVisible = true;
      this.form = JSON.parse(JSON.stringify(scope.row));
      this.editIndex = scope.$index;
    },
    confirmEditField(form) {
      this.$set(this.fields, this.editIndex, form);
      this.modalVisible = false;
    },
    handleCheckAllChange(event) {
      this.fields = event.target.checked
        ? this.storeApiMsg.column.map(item => {
          return {
            columnName: item.columnName,
            filter: true,
            fill: false,
            defaultValue: '',
            type: item.type,
            useDefaultValue: false
          };
        })
        : [];
      this.isIndeterminate = false;
    },
    changeCheckAllStatus() {
      const checkedCount = this.fields.length;
      this.checkAll = checkedCount > 0;
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.storeApiMsg.column.length;
    }
  }
};
</script>



