<template>
  <div class="test-api">
    <div class="content">
      <p class="title">
        <span>API地址</span>
        <span class="path">{{ storeApiMsg.path }}</span>
      </p>
      <div class="req-msg">
        <h5 class="title">请求信息</h5>
        <div class="request">
          <div
            v-for="(item, index) in testData"
            :key="index"
            class="fields"
          >
            <gs-checkbox
              v-model="item.check"
              class="name"
            >
              <span
                :title="item.columnName"
              >{{ item.columnName }}</span>
            </gs-checkbox>
            <span>=</span>
            <span class="value">
              <fields-input
                v-model="item.value"
                :type="item.type"
              />
            </span>
          </div>
          <div class="fields">
            <span class="name">limit</span>
            <span>=</span>
            <span class="value">
              <gs-input-number
                :precision="0"
                v-model="limit"
                :min="0"
                :max="100"
              />
            </span>
          </div>
          <div class="fields">
            <span class="name">offset</span>
            <span>=</span>
            <span class="value">
              <gs-input-number
                :precision="0"
                v-model="offset"
                :min="0"
              />
            </span>
          </div>
        </div>
        <div class="test-operation">
          <gs-button
            :disabled="!btnLoading"
            @click="stopTest"
          >停止测试</gs-button>
          <gs-button
            :loading="btnLoading"
            type="primary"
            @click="startTest"
          >开始测试</gs-button>
        </div>
      </div>
      <div class="rep-res">
        <div class="response">
          <gs-tabs
            v-model="activeName"
            type="content-tab"
          >
            <gs-tab-pane
              label="编码模式"
              name="json"
            >
              <gs-textarea
                :value="testResStringify"
                :autosize="{
                  minRows: 8,
                  maxRows: 12
                }"
                readonly
              />
            </gs-tab-pane>
            <gs-tab-pane
              label="表格模式"
              name="table"
            >
              <el-table
                :data="testRes"
              >
                <el-table-column
                  v-for="(item, index) in tableHeader"
                  :key="index"
                  :label="item"
                  :prop="item"
                >
                  <template slot-scope="scope">
                    {{ scope.row[item] + '' }}
                  </template>
                </el-table-column>
              </el-table>
            </gs-tab-pane>
          </gs-tabs>
        </div>
      </div>
    </div>
    <div class="operation">
      <gs-button
        @click="handleBack"
      >上一步</gs-button>
      <gs-button
        type="primary"
        @click="handleNext"
      >完成</gs-button>
    </div>
  </div>
</template>
<script>
import stepMixins from '../mixin.js';
import testMixins from 'src/components/api-test-modal/api-test-mixins';
import * as service from 'src/service/data-share/index';
import axios from 'axios';
import fieldsInput from 'src/components/fields-input';
let randomName = Math.random().toString(36).slice(-8);
export default {
  components: {
    fieldsInput
  },
  mixins: [stepMixins, testMixins],
  data() {
    return {
    };
  },
  computed: {
  },
  activated() {
    this.testData = (this.storeApiMsg.fields || [])
      .filter(item => item.filter)
      .map(item => {
        const column = (this.storeApiMsg.column || []).find(i => i.columnName === item.columnName);
        return {columnName: item.columnName, value: item.defaultValue || '', check: true, type: column.type, fill: item.fill};
      });
  },
  methods: {
    handleNext() {
      this.$emit('next');
    },
    handleBack() {
      this.$emit('back');
    },
    startTest() {
      if (this.checkTestInput()) {
        const {
          tableGuid,
          dbGuid,
          maxRowNumber
        } = this.storeApiMsg;
        const parameter = this.testData.filter(item => {
          const column = (this.storeApiMsg.fields || []).find(i => i.columnName === item.columnName);
          return item.check || (!item.fill && column.defaultValue !== '');
        })
          .map(item => {
            return {columnName: item.columnName, value: [item.value]};
          });
        const queryFields = this.storeApiMsg.column.map(item => item.columnName);
        const params = {
          tableGuid,
          dbGuid,
          maxRowNumber,
          parameter,
          queryFields,
          limit: this.limit,
          offset: this.offset
        };
        randomName = Math.random().toString(36).slice(-8);
        this.btnLoading = true;
        const { CancelToken } = axios;
        this.source = CancelToken.source();
        service.testApi({params, randomName}, this.source.token).then(data => {
          this.testRes = data;
          this.btnLoading = false;
        }).catch(e => {
          this.btnLoading = false;
          console.log('Request canceled', e.message);
        });
      }
    }
  }
};
</script>



