<template>
  <div
    v-loading="loading"
    class="base-info"
  >
    <gs-form
      ref="form"
      :model="form"
      :rules="rules"
      label-position="top"
    >
      <gs-form-item
        label="API名称"
        prop="name"
      >
        <gs-input
          v-model="form.name"
          placeholder="请填写API名称"
        />
      </gs-form-item>
      <gs-form-item
        label="API分组"
        prop="groupGuid"
      >
        <gs-select
          v-model="form.groupGuid"
          placeholder="请选择API分组"
          searchable
        >
          <gs-option
            v-for="item in groupOptions"
            :key="item.guid"
            :label="item.name"
            :value="item.guid"
          />
        </gs-select>
      </gs-form-item>
      <gs-form-item
        label="版本"
        prop="version"
      >
        <gs-input-number
          v-model="form.version"
          :min="0"
          placeholder="请填写API版本"
        />
      </gs-form-item>
      <gs-form-item
        label="描述"
        prop="description"
      >
        <gs-textarea
          v-model="form.description"
          placeholder="请填写描述信息"
        />
      </gs-form-item>
    </gs-form>
    <div class="info">
      <div>
        <span>协议类型</span>
        <span>{{ form.protocol }}</span>
      </div>
      <div>
        <span>请求方式</span>
        <span>{{ form.requestMode }}</span>
      </div>
      <div>
        <span>返回类型</span>
        <span>{{ form.returnType }}</span>
      </div>
      <div>
        <span>API路径</span>
        <span>{{ form.path }}</span>
      </div>
    </div>
    <div class="operation">
      <gs-button
        @click="handleBack"
      >上一步</gs-button>
      <gs-button
        type="primary"
        @click="handleNext"
      >下一步</gs-button>
    </div>
  </div>
</template>
<script>
import mixins from '../mixin.js';
import * as service from 'src/service/data-share/index';
import {mapState} from 'vuex';
export default {
  mixins: [mixins],
  props: {
    data: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      form: {
        name: '',
        groupGuid: '',
        version: 1,
        description: '',
        path: '',
        returnType: 'JSON',
        protocol: 'HTTP',
        requestMode: 'POST'
      },
      getRandString: new Date().getTime().toString(36),
      rules: {
        name: [{required: true, trigger: 'blur', message: '请填写API名称'}],
        groupGuid: [{required: true, trigger: 'change', message: '请选择API分组'}],
        version: [{required: true, trigger: 'blur', message: '请填写API版本', type: 'number'}]
      }
    };
  },
  computed: {
    ...mapState('datashare', [
      'sourceGroupTree'
    ]),
    groupOptions: {
      set(val) {
        return val;
      },
      get() {
        if (this.sourceGroupTree.length) {
          return this.sourceGroupTree.filter(item => item.parentGuid);
        }
      }
    }
  },
  watch: {
    'form.version': {
      handler(val) {
        if (this.$route.query.isEdit) {
          const string = this.data.path.split('/').reverse()[0];
          this.form.path = '/api/v' + val + '/share/' + string;
        } else {
          this.form.path = '/api/v' + val + '/share/' + this.getRandString;
        }
      },
      immediate: true
    }
  },
  created() {
    this.fetchGroupList();
    if (this.data && Object.keys(this.data).length) {
      this.setData();
    }
  },
  methods: {
    setData() {
      const {
        name,
        groupGuid,
        protocol,
        requestMode,
        returnType,
        version,
        description
      } = this.data;
      this.form = {
        ...this.form,
        name: this.$route.query.isEdit ? name : '',
        groupGuid,
        version: Number(version),
        description,
        returnType,
        protocol,
        requestMode
      };
    },
    fetchGroupList() {
      this.loading = true;
      if (this.sourceGroupTree && this.sourceGroupTree.length) {
        this.groupOptions = this.sourceGroupTree.filter(item => item.parentGuid);
        this.loading = false;
      } else {
        service.getGroupList().then(data => {
          this.groupOptions = data.filter(item => item.parentGuid);
          this.loading = false;
        });
      }
    },
    handleBack() {
      this.$emit('back');
    },
    handleNext() {
      this.$refs.form.validate(valid => {
        if (valid) {
          if (this.data &&
          Object.keys(this.data).length &&
          this.form.name === this.data.name &&
          this.$route.query.isEdit
          ) {
            this.saveApiMsg(this.form);
            this.$emit('next');
          } else {
            service.checkName({name: this.form.name}).then(data => {
              if (!data) {
                this.saveApiMsg(this.form);
                this.$emit('next');
              } else {
                this.$Message.error('已存在该名称');
              }
            });
          }
        }
      });
    }
  }
};
</script>



